<template>
  <div id="app" ref="app" class="theme-red">
    <ju-header></ju-header>
    <main class="main">
      <router-view/>
    </main>
    <ju-footer></ju-footer>
  </div>
</template>

<script>
import { JuHeader,JuFooter } from './components/layout'
import { mapGetters } from 'vuex'
export default {
  name: 'app',
  //导入getters取得state放入计算属性中
  computed: {
    ...mapGetters(['themeCls'])
  },
  mounted(){
    this.setTheme()
  },
  watch:{
    themeCls(newVal){
      newVal ? this.setTheme():null
    }
  },
  methods:{
    setTheme(){
      const target = this.$refs.app
      const localStorageCls = localStorage.getItem('theme') ? localStorage.getItem('theme') : 'red'
      target.className = this.themeCls ? 'theme-' + this.themeCls
                                  : `theme-${localStorageCls}`
    }
  },
  components:{
    JuHeader,
    JuFooter
  }
}
</script>

<style>
#app {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.main {
  flex: 1 0 auto;
}
</style>



