import Vue from 'vue'
import Router from 'vue-router'
import Home from 'pages/home/home'

// 精准招商平台开始
const AIIndex = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/index')
const AIHome = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/home')
const AIDash = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/dash')
const newlyBorn = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/newlyBorn')
const knownCelebrity = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/knownCelebrity')
const kindsOfPlan = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/kindsOfPlan')
const blackEnterprise = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/blackEnterprise')
const beListed = () => import(/* webpackChunkName: "group-ai" */ 'pages/accurateInvestment/beListed')
// 精准招商平台结束

// 产业链招商平台开始
const industryChain = () => import(/* webpackChunkName: "group-ic" */ 'pages/industryChain/index')
const occupationRatioRAnalysis = () =>
  import(/* webpackChunkName: "group-ic" */ 'pages/industryChain/occupationRatioRAnalysis')
const distributionManagement = () =>
  import(/* webpackChunkName: "group-ic" */ 'pages/industryChain/distributionManagement')
const complementationOfIndustrial = () =>
  import(/* webpackChunkName: "group-ic" */ 'pages/industryChain/complementationOfIndustrial')
const upstreamAndDownstream = () =>
  import(/* webpackChunkName: "group-ic" */ 'pages/industryChain/upstreamAndDownstream')
const industryAnalysisReport = () =>
  import(/* webpackChunkName: "group-ic" */ 'pages/industryChain/industryAnalysisReport')
// 产业链招商平台结束

import Building from 'components/building'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', //路由默认跳转
      redirect: '/accurateInvestment/home'
    },
    //精准招商平台
    {
      path: '/accurateInvestment',
      name: 'accurateInvestment',
      component: AIIndex,
      children: [
        { path: 'home', name: 'home', component: AIHome },
        {
          path: '',
          name: 'dash',
          component: AIDash,
          children: [
            {
              path: 'newlyBorn',
              name: 'newlyBorn',
              component: newlyBorn
            },
            {
              path: 'knownCelebrity',
              name: 'knownCelebrity',
              component: knownCelebrity
            },
            {
              path: 'kindsOfPlan',
              name: 'kindsOfPlan',
              component: kindsOfPlan
            },
            {
              path: 'blackEnterprise',
              name: 'blackEnterprise',
              component: blackEnterprise
            },
            {
              path: 'beListed',
              name: 'beListed',
              component: beListed
            }
          ]
        }
      ]
    },
    //产业链招商平台
    {
      path: '/industryChain',
      name: 'industryChain',
      redirect: '/industryChain/distributionManagement',
      component: industryChain,
      children: [
        {
          path: 'distributionManagement',
          name: 'distributionManagement',
          component: distributionManagement
        },
        {
          path: 'occupationRatioRAnalysis',
          name: 'occupationRatioRAnalysis',
          component: occupationRatioRAnalysis
        },
        {
          path: 'complementationOfIndustrial',
          name: 'complementationOfIndustrial',
          component: complementationOfIndustrial
        },
        {
          path: 'upstreamAndDownstream',
          name: 'upstreamAndDownstream',
          component: Building
        },
        {
          path: 'industryAnalysisReport',
          name: 'industryAnalysisReport',
          component: Building
        }
      ]
    },
    {
      path: '**',
      name: '建设中',
      component: Building
    }
  ],
  linkActiveClass: 'active'
})



