<template>
  <header class="header">
    <div class="header-t">
      <div class="userinfo">
        <span class="username">你好，张丽</span>
        <span class="usericon">
          <i class="fa fa-sign-out" aria-hidden="true"></i>
        </span>
      </div>
    </div>
    <div class="header-b">
      <div class="menuinfo">
        <div class="brand">
          <span>政府招商管理后台</span>
        </div>
        <el-menu :default-active="activeIndex" class="menu" mode="horizontal" @select="handleSelect">
          <el-menu-item index="1">首页</el-menu-item>
          <el-menu-item index="2">精准招商平台</el-menu-item>
          <el-menu-item index="3">企业信息审查平台</el-menu-item>
          <el-menu-item index="4">企业信息监管平台</el-menu-item>
          <el-menu-item index="5">产业链招商平台</el-menu-item>
          <el-menu-item index="6">企业信用平台</el-menu-item>
          <el-menu-item index="7">招商业务督办平台</el-menu-item>
          <el-menu-item index="8">用户管理</el-menu-item>
        </el-menu>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name:'JuHeader',
  data() {
    return {
      activeIndex: '1'
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      switch(parseInt(key)) {
        case 2:
          this.$router.push('/accurateInvestment/home')
          break
        case 5:
          this.$router.push('/industryChain')
          break
        default:
          this.$router.push('/build')
          break
      }
      // console.log(key, keyPath)
    }
  }

}
</script>

<style lang="scss" scoped>
  .header {
    display: flex;
    flex-direction: column;
  }

  .header-t {
    background-image: linear-gradient(#1c354a, #1c354a);
    .userinfo {
      width: 1200px;
      margin: 0 auto;
      display: flex;
      justify-content: flex-end;
    }
    .username {
      font-family: MicrosoftYaHei;
      font-size: 12px;
      height: 21px;
      line-height: 21px;
      letter-spacing: 0px;
      color: rgba(255, 255, 255, 0.5);
    }
    .usericon {
      margin-left: 30px;
      height: 21px;
      color: #d8d8d8;
      cursor: pointer;
    }
  }

  .header-b {
    background-image: linear-gradient(#015293, #015293);
    .menuinfo {
      width: 1200px;
      margin: 0 auto;
      display: flex;
    }
    .brand {
      height: 55px;
      line-height: 55px;
      font-family: Tensentype-XiChaoHeiJ;
      font-size: 30px;
      letter-spacing: 0px;
      color: #f1faff;
    }
    .menu {
      background-color: #015293;
    }
    /deep/ .el-menu--horizontal {
      border-bottom: none;
      margin-left: 45px;
      .el-menu-item {
        height: 55px;
        line-height: 55px;
        font-size: 14px;
        color: #fff;
        border-bottom: none;
        padding: 0 13px;
        &:hover,
        &.is-active {
          background-color: #006dc5;
        }
      }
    }
  }
</style>



